import ErrorMsg from '@/components/error-msg'
import { createArticle, updateArticle } from '@/api/article'

export default {
  // 在路由匹配组件渲染之前先执行中间件处理，有多个定义成数组
  middleware: 'authenticated',
  name: "EditorIndex",
  data() {
    return {
      post: {
        title: '',
        description: '',
        body: '',
        tagList: []
      },
      newTag: '',
      errors: []
    }
  },

  mounted() {
    // debugger
    console.log(this.$route.params);
    
    if (!this.$route.params) return
    this.post = { ...this.$route.params }
  },

  components: {
    ErrorMsg
  },

  methods: {
    addTag() {
      this.post.tagList && this.post.tagList.push(this.newTag)
      this.newTag = ''
    },
    removeTag(tag, idx) {
      this.post.tagList && this.post.tagList.splice(idx, 1)
    },
    async onSubmit() {
      try {
        const { data } = this.post.slug ? await updateArticle({
          slug: this.post.slug, // 更新时才有
          article: this.post
        }) : await createArticle({
          article: this.post
        })
        
        this.$router.push({
          name: 'article',
          params: {
            slug: data.article.slug
          }
        })
      } catch (err) {
        this.errors = err.response.data.errors || []
      }
    }
  }
};
